.footer {
  border-top: 1px solid var(--color-gray);
  padding: 30px 0;

  &__inner {
    @include flex-all-sb;

    @include for-width(-mobile-l) {
      display: block;
    }
  }

  .logo {
    @include for-width(-mobile-l) {
      margin-bottom: 15px;
    }
  }

  .nav {
    &__list {
      @include for-width(-mobile-md) {
        display: block;
        margin: 0 auto;
        max-width: 200px;
      }
    }

    &__item {
      &:not(:last-child) {
        margin-right: clamp(20px, rem(45), 455px);

        @include for-width(-tablet) {
          margin-right: 20px;
        }

        @include for-width(-mobile-md) {
          margin-right: 0;
          margin-bottom: 20px;
          text-align: center;
        }
      }
    }
  }

  .social {
    @include for-width(-mobile-md) {
      justify-content: center;
    }
  }
}
