.header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  background-color: var(--color-white);
  transition: top 0.3s ease;

  &__inner {
    @include flex-all-sb;

    padding: 24px 0;

    @include for-width(-mobile-l) {
      display: block;
    }
  }

  &.hide {
    top: calc(var(--header-height) * -1);
  }
}

.nav {
  &__list {
    @include flex-all-sb;

    @include for-width(-mobile-md) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      margin: 0 auto;
      max-width: 280px;
      gap: 10px;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: clamp(20px, rem(40), 45px);

      @include for-width(-tablet) {
        margin-right: 20px;
      }
    }

    &:last-child {
      margin-right: 20px;
    }

    &:nth-child(3) {
      @include for-width(-mobile-md) {
        margin-right: 0;
      }
    }

    &--donate {
      grid-column: 1/3;
    }
  }

  &__link {
    white-space: nowrap;
  }
}
