/* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
.page {
  height: 100vh;
  font-family: var(--font-family);
  font-weight: 500;
  font-style: normal;
  font-feature-settings: "pnum" on, "lnum" on;
  font-size: 13px;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
  color: var(--color-dark);
  background-color: var(--color-white);
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
  scroll-padding-top: var(--scroll-padding, 120px);
}
.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.site-container {
  overflow: hidden;
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.main {
  padding-top: var(--header-height);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.logo {
  margin-right: 30px;
  width: 100%;
  max-width: 240px;
}
@media (max-width: calc(62em - 0.0625em)) {
  .logo {
    max-width: 200px;
  }
}
@media (max-width: calc(48em - 0.0625em)) {
  .logo {
    margin: 0 auto;
  }
}

.link-colored {
  display: inline-block;
  border-radius: var(--radius-main);
  padding: 9px 35px;
  font-weight: 700;
  color: var(--color-dark);
  background-color: var(--color-yellow);
}

.content {
  font-weight: 400;
  font-size: 13px;
  line-height: 1.7;
}
.content p:not(:last-child) {
  margin-bottom: 30px;
}

.title {
  font-weight: 700;
  font-size: 26px;
  line-height: 1.55;
  color: var(--color-dark);
}

.title-sec {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.7;
  color: var(--color-dark-900);
}

.black {
  font-weight: 700;
  color: var(--color-dark);
}

.header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  background-color: var(--color-white);
  transition: top 0.3s ease;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 0;
}
@media (max-width: calc(48em - 0.0625em)) {
  .header__inner {
    display: block;
  }
}
.header.hide {
  top: calc(var(--header-height) * -1);
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: calc(36em - 0.0625em)) {
  .nav__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 0 auto;
    max-width: 280px;
    gap: 10px;
  }
}
.nav__item:not(:last-child) {
  margin-right: clamp(20px, 2.5rem, 45px);
}
@media (max-width: calc(62em - 0.0625em)) {
  .nav__item:not(:last-child) {
    margin-right: 20px;
  }
}
.nav__item:last-child {
  margin-right: 20px;
}
@media (max-width: calc(36em - 0.0625em)) {
  .nav__item:nth-child(3) {
    margin-right: 0;
  }
}
.nav__item--donate {
  grid-column: 1/3;
}
.nav__link {
  white-space: nowrap;
}

.social {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.social__item:not(:last-child) {
  margin-right: 20px;
}
.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-main);
  width: 37px;
  height: 37px;
  background-color: var(--color-gray);
}
.social__link--fb .social__icon {
  width: 9px;
}
.social__icon {
  width: 21px;
  height: 21px;
  fill: var(--color-white);
}

.hero {
  background-image: url("../img/hero.jpg");
  /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
  background-image: image-set("../img/hero.avif" type("image/avif"), "../img/hero.webp" type("image/webp"), "../img/hero.jpg" type("image/jpg"));
  position: relative;
  height: clamp(400px, 49.6875rem, 795px);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.6;
}
.hero__inner {
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 1;
  flex-direction: column;
  height: 100%;
  color: var(--color-white);
}
.hero__content {
  width: 100%;
  max-width: 840px;
}
.hero__title {
  font-weight: 700;
  font-size: clamp(18px, 2.0625rem, 35px);
  line-height: 1.5;
}
.hero__text {
  width: 100%;
  max-width: 620px;
}
.hero *:not(:last-child) {
  margin-bottom: clamp(20px, 2.5rem, 45px);
}
@media (max-width: calc(48em - 0.0625em)) {
  .hero {
    height: 500px;
  }
}

.about {
  margin-bottom: 9.375rem;
  padding-top: 9.375rem;
}
.about__inner {
  display: grid;
  grid-template-columns: 510px 1fr;
  gap: 4.375rem;
}
@media (max-width: calc(62em - 0.0625em)) {
  .about__inner {
    display: flex;
    flex-direction: column-reverse;
  }
}
.about__title {
  margin-bottom: 15px;
}
.about__pictures {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: clamp(15px, 1.875rem, 40px);
}
@media (max-width: calc(62em - 0.0625em)) {
  .about__pictures {
    justify-content: center;
    grid-template-columns: repeat(2, 300px);
  }
}
@media (max-width: calc(36em - 0.0625em)) {
  .about__pictures {
    grid-template-columns: repeat(2, 1fr);
  }
}
.about__image {
  height: 100%;
}
.about__picture {
  position: relative;
}
.about__picture::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.3;
}
@media (max-width: calc(48em - 0.0625em)) {
  .about {
    margin-bottom: 5rem;
    padding-top: 5rem;
  }
}

.donate {
  margin-bottom: 7.5rem;
}
.donate__title {
  margin-bottom: 3.75rem;
  text-align: center;
}
.donate__item {
  border-radius: var(--radius-main);
  background-color: var(--color-white);
}
.donate__item:not(:last-child) {
  margin-bottom: 20px;
}
.donate__faq {
  width: 100%;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.8;
  letter-spacing: 0.01em;
}
.donate__faq summary {
  position: relative;
  border: 1px solid var(--color-gray);
  padding: 2.5rem 5rem;
  width: 100%;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  letter-spacing: 0.01em;
  color: var(--color-dark);
  cursor: pointer;
}
.donate__faq summary::before {
  content: "";
  position: absolute;
  left: 30px;
  top: 50%;
  border-radius: 1px;
  width: 20px;
  height: 4px;
  background-color: var(--color-blue);
  transform: translateY(-50%);
}
.donate__faq summary::after {
  content: "";
  position: absolute;
  left: 40px;
  top: 50%;
  border-radius: 1px;
  width: 4px;
  height: 20px;
  background-color: var(--color-blue);
  transform: translate(-50%, -50%) rotate(0);
  transition: transform 0.1s linear, background-color 0.1s linear;
}
@media (max-width: calc(48em - 0.0625em)) {
  .donate__faq summary {
    padding: 2.5rem 3.125rem;
  }
}
@media (max-width: calc(48em - 0.0625em)) {
  .donate__faq summary {
    text-align: center;
  }
}
.donate__faq[open] summary::after {
  transform: translate(-50%, -50%) rotate(90deg);
}
.donate__content {
  border: 1px solid var(--color-gray);
  border-top: none;
  padding: 3.125rem 5rem;
  width: 100%;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.5;
}
.donate__content p:not(:last-child) {
  margin-bottom: 1.875rem;
}
.donate__content--double {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3.75rem clamp(50px, 10rem, 160px);
}
.donate__content--double .donate__list {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 3.75rem 0;
}
@media (max-width: calc(62em - 0.0625em)) {
  .donate__content--double {
    gap: 3.75rem;
  }
}
@media (max-width: calc(48em - 0.0625em)) {
  .donate__content--double {
    grid-template-columns: 1fr;
  }
}
@media (max-width: calc(80em - 0.0625em)) {
  .donate__content {
    padding: 2.5rem 3.125rem;
  }
}
@media (max-width: calc(23.4375em - 0.0625em)) {
  .donate__content {
    padding: 1.875rem 10px;
  }
}
.donate__list {
  display: grid;
  gap: 30px 0;
}
.donate__list-item > span {
  display: block;
  margin-bottom: 13px;
}
.donate__copy {
  display: inline-flex;
}
.donate__copy span {
  margin-right: 10px;
}
@media (max-width: calc(62em - 0.0625em)) {
  .donate__copy span {
    max-width: 250px;
  }
}
.donate__copy-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.donate__copy-icon {
  width: 16px;
  height: 16px;
}
.donate__swift {
  padding-top: 20px;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;
}
.donate .gold {
  color: var(--color-yellow);
}
@media (max-width: calc(36em - 0.0625em)) {
  .donate {
    margin-bottom: 5rem;
  }
}

.choose {
  margin-bottom: 80px;
}
.choose__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
}
@media (max-width: calc(48em - 0.0625em)) {
  .choose__list {
    grid-template-columns: 1fr;
  }
}
.choose__title {
  margin-bottom: 40px;
  text-align: center;
}
.choose__picture {
  margin-bottom: 35px;
  width: 100%;
  max-width: 580px;
}
@media (max-width: calc(48em - 0.0625em)) {
  .choose__picture {
    margin: 0 auto 30px;
  }
}
.choose__item-title {
  margin-bottom: 15px;
}
.choose__content {
  margin-bottom: 30px;
}

.footer {
  border-top: 1px solid var(--color-gray);
  padding: 30px 0;
}
.footer__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: calc(48em - 0.0625em)) {
  .footer__inner {
    display: block;
  }
}
@media (max-width: calc(48em - 0.0625em)) {
  .footer .logo {
    margin-bottom: 15px;
  }
}
@media (max-width: calc(36em - 0.0625em)) {
  .footer .nav__list {
    display: block;
    margin: 0 auto;
    max-width: 200px;
  }
}
.footer .nav__item:not(:last-child) {
  margin-right: clamp(20px, 2.8125rem, 455px);
}
@media (max-width: calc(62em - 0.0625em)) {
  .footer .nav__item:not(:last-child) {
    margin-right: 20px;
  }
}
@media (max-width: calc(36em - 0.0625em)) {
  .footer .nav__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
    text-align: center;
  }
}
@media (max-width: calc(36em - 0.0625em)) {
  .footer .social {
    justify-content: center;
  }
}